<template>
    <div class="container">
        <div class="row">
            <div >
                <h1 class="main-title">電影趨勢榜前20名</h1>
            </div>
            <ul>
                <li v-for="(item,index) in arr" :key="index" class="wrap">
                    <h1 class="title">{{index+1}} . {{item.title}}</h1>
                    <img :src="bindUrl(item.poster_path)" alt="">
                    <h4 class="popular"> 人氣 : {{item.popularity}}</h4>
                    <p class="content">{{item.overview}}</p>
                    
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

import axios from 'axios'
export default {
    data() {
        return{
            arr:[]
        }
    },
    methods:{
        bindUrl(url){
            return "https://image.tmdb.org/t/p/original"+url
            // return `https://picsum.photos/300/200/?random=${10}`
        }
    },
    mounted(){
        axios.get('https://api.themoviedb.org/3/trending/movie/week?api_key=a5e1f3082bd9617d50b28dcb7d42f23a').then(
            response=>{
                // console.log(response)
                this.arr = response.data.results
            },
            error=>{
                // console.log(error.message)
            }
        )
    }
}
</script>

<style scoped>
    .container{
        width: 50%;
        height: 50%;
        /* display: none; */
    }
    .main-title{
        /* font-size: 100px; */
        font-size: 68px;
        font-weight: 700;
        margin-top: 50px;
    }
    .wrap{
        /* border: 1px solid #f00; */
        margin: 80px 0;
    }
    .title{
        margin: 20px 0px 0px 0px;
        padding: 30px;
        font-weight: 700;
        background-color: #000;
        color: #fff;
    }
    img{
            width: 100%;
            height: 100%;    
    }
    .content{
        /* border: 1px solid #000; */
        margin: 0px 0px 30px 0px;
        padding: 30px;
        font-size: 20px;
        text-align: left;
        background-color: #eee;

        
    }
    .content::first-letter{
        font-size: 0%;
    }
    .popular{
        font-weight:600;
        margin: 0;
        /* margin: 20px 0 ; */
        padding: 20px 0;
        /* background-color: rgb(196, 241, 243);  */
        background-color: #777;
        color: #fff;


    }
    @media screen and (max-width : 991px) {
        .container{
            width: 100%;
            height: 100%;
        }
    }
</style>
</style>