<template>
    <div class="container">
        <div class="wrap row">
            <div class="col-12">
                <img  v-show="imgDone" :src="url"  alt="img" id="img" class="col apiImg my-auto" @load="open">
                <el-skeleton-item v-show="!(imgDone)" variant="image" class="my-auto" style="width: 100%; height: 100%;" />
                <p class="col-12 content" v-show="!(imgDone)" >載入檔案時請等待</p>
                <h5 class="col-12 content"> 來源 :  {{source}}</h5>
                <button @click="getapi" class="btn mx-auto  mt-5 mb-5">隨機取得圖片</button>
            </div>
        </div>
       

    </div>
</template>

<script>
import axios from 'axios'

export default {
    data(){
        return{
            url:'',
            source:'',
            imgDone:false
        }
    },
    methods:{
        getapi(){
            axios.get('https://api.waifu.im/random').then(
                response =>{
                    // console.log(response)
                    this.imgDone=false
                    this.url = response.data.images[0].url
                    this.source = response.data.images[0].source

                },
                error=>{
                    console.log(error.message)
                }
            )
        },
        open(){
            alert('載入完畢')
            // console.log('載入完畢')
            this.imgDone=true
        }
    }

}
</script>

<style scoped>
.btn{
    height: 60px;
    width: 250px;
    border: 3px solid #ccc;

}
.wrap{
    display: flex;
}
.apiImg{
    width: 100%;
    height: 100%;
}
.content{
    margin-top: 20px;
}
</style>