<template>
    <div class="container">
        <div class="row">
            <div >
                <h1 class="main-title">人物趨勢榜前20名</h1>
            </div>
            <ul class="col wrap ">
                <li v-for="(item,index) in arr" :key="index" class="item ms-4 mx-auto">
                    <h3 class="title">{{item.name}}</h3>
                    <img :src="bindUrl(item.profile_path)" alt="">
                    <h4 class="popular"> 人氣 : {{item.popularity}}</h4>
                    <p class="content">{{item.known_for_department}}</p>
                    
                </li>
            </ul>
        </div>
    </div>
</template>

<script>

import axios from 'axios'
export default {
    data() {
        return{
            arr:[]
        }
    },
    methods:{
        bindUrl(url){
            return "https://image.tmdb.org/t/p/original"+url
            // return `https://picsum.photos/300/200/?random=${10}`
        }
    },
    mounted(){
        axios.get('https://api.themoviedb.org/3/trending/person/week?api_key=a5e1f3082bd9617d50b28dcb7d42f23a').then(
            response=>{
                // console.log(response)
                this.arr = response.data.results
            },
            error=>{
                // console.log(error.message)
            }
        )
    }
}
</script>

