<template>
    <div class="container">
            <el-carousel :interval="4000" type="card" class="card"   height="500px" >
                <el-carousel-item v-for="(item,index) in arr" :key="index" class="cc">
                <!-- <h3 class="medium">{{ item }}</h3> -->
                    <img :src="bindUrl(item.backdrop_path)" alt="">
                </el-carousel-item>
            </el-carousel>
            <h1>快速了解電影及電視排行</h1>
    </div>
  <!-- <h1>電影排行</h1> -->
</template>

<script>
import axios from 'axios'
export default {
    data() {
        return{
            arr:[]

        }
    },
    methods:{
        bindUrl(url){
            return "https://image.tmdb.org/t/p/original"+url
        }
    },
    mounted(){
        axios.get('https://api.themoviedb.org/3/discover/movie?api_key=a5e1f3082bd9617d50b28dcb7d42f23a&language=zh-TW&sort_by=popularity.desc&include_adult=false&include_video=false&page=1&year=2022&with_watch_monetization_types=flatrate').then(
            response=>{
                // console.log(response)
                this.arr = response.data.results
                // console.log(response.data.results[0].backdrop_path)
            }
        )
    }
}
</script>

<style scoped>
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
    img{
        width: 100%;
        height: 100%;
    }
    h1{
        margin: 100px 0;
        font-weight: 700;
    }
    @media screen and (max-width:767px) {
        .card{
            height: 300px;
            width: 100%;
        }
    }
</style>